import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import NewOrPreowned from './newOrPreowned';
import DieselOrGas from './dieselOrGas';
import Manufacturer from './manufacturer';
import Floorplan from './floorplan';
import Model from './model';
import { withRouter } from "react-router";


const styles = theme => ({
  root: {
    width: '90%',
  },
  button: {
    marginRight: theme.spacing.unit,
  },
  instructions: {
    marginTop: theme.spacing.unit,
    marginBottom: theme.spacing.unit,
  },
});
console.log(sessionStorage.getItem("newOrPreowned"))
class HorizontalLinearStepper extends React.Component {
  state = {
    activeStep: 0,
    skipped: new Set(),
    nextStepDisabled: sessionStorage.getItem("newOrPreowned") === null,
    // dieselOrGas: sessionStorage.getItem("dieselOrGas"),      
    // value: sessionStorage.getItem("floorplan"), 
    // manufacturer: sessionStorage.getItem("manufacturer"),
    // model: sessionStorage.getItem("model"),
    // newOrPreowned: sessionStorage.getItem("newOrPreowned"),
    // year: sessionStorage.getItem('year')
  }

  isNewOrPreowned = step => {
    return step === 0
  }

  isStepOptional = step => {
    return false
  }

  handleCard = () => {
    this.props.history.push('/card/' + 
      sessionStorage.getItem("dieselOrGas") + "/" + 
      sessionStorage.getItem("manufacturer").replace(/\s/g,'') + "/" + 
      sessionStorage.getItem('year') + "/" +
      sessionStorage.getItem('model').replace(/\s/g,'') + "/" +
      sessionStorage.getItem('floorplan'))
  }

  handleNext = () => {
    const { activeStep } = this.state
    let { skipped } = this.state
    if (this.isStepSkipped(activeStep)) {
      skipped = new Set(skipped.values())
      skipped.delete(activeStep)
    }
    this.setState({
      activeStep: activeStep + 1,
      skipped,
    })
    this.setNextStepDisabled(activeStep + 1)
  }

  handleBack = () => {
    this.setState(state => ({
      activeStep: state.activeStep - 1,
      nextStepDisabled: false
    }));    
  };

  handleEnableNextStep = () => {
    this.setState(state => ({
      nextStepDisabled: false
    }));
  }

  handleSkip = () => {
    const { activeStep } = this.state;
    if (!this.isStepOptional(activeStep)) {
      // You probably want to guard against something like this,
      // it should never occur unless someone's actively trying to break something.
      throw new Error("You can't skip a step that isn't optional.");
    }

    this.setState(state => {
      const skipped = new Set(state.skipped.values());
      skipped.add(activeStep);
      return {
        activeStep: state.activeStep + 1,
        skipped,
      };
    });
  };

  handleReset = () => {
    this.setState({
      activeStep: 0,
    });
  };


  isStepSkipped(step) {
    return this.state.skipped.has(step);
  }

  setNextStepDisabled(step) {
    switch (step) {
      case 0:
        if (!this.state.nextStepDisabled && sessionStorage.getItem("newOrPreowned") === null) {
          this.setState({nextStepDisabled: true})
        } 
        else if (this.state.nextStepDisabled && sessionStorage.getItem("newOrPreowned") !== null) {
          this.setState({nextStepDisabled: false})
        }               
        break;
      case 1:        
        if (!this.state.nextStepDisabled && sessionStorage.getItem("dieselOrGas") === null) {         
          this.setState({nextStepDisabled: true})
        }
        else if (this.state.nextStepDisabled && sessionStorage.getItem("dieselOrGas") !== null) {
          this.setState({nextStepDisabled: false})
        }
        break;
      case 2:      
        if (!this.state.nextStepDisabled && sessionStorage.getItem("manufacturer") === null) {
           this.setState({nextStepDisabled: true})
        }
        else if (this.state.nextStepDisabled && sessionStorage.getItem("manufacturer") !== null) {
          this.setState({nextStepDisabled: false})
        }
        break
      case 3:        
        if (!this.state.nextStepDisabled && sessionStorage.getItem("model") === null) {
          this.setState({nextStepDisabled: true})
        }
        else if (this.state.nextStepDisabled && sessionStorage.getItem("model") !== null) {
          this.setState({nextStepDisabled: false})
        }
        break     
      case 4:
        if (!this.state.nextStepDisabled && sessionStorage.getItem("floorplan") === null) {
          this.setState({nextStepDisabled: true})
        }
        else if (this.state.nextStepDisabled && sessionStorage.getItem("floorplan") !== null) {
          this.setState({nextStepDisabled: false})
        }
      break;   
      default: return false
    }
  }
  getSteps = () => {
    return ['Select New or Pre-Order', 'Select Diesel or Gas', 'Which Manufacturer', 'Select your Model', 'Select Floorplan'];
  }


  getStepContent = (step) => {
    switch (step) {
      case 0:
        return <NewOrPreowned handlerToUpdate = {this.handleEnableNextStep} />;
      case 1:
        return <DieselOrGas handlerToUpdate = {this.handleEnableNextStep} />;
      case 2:
        return <Manufacturer handlerToUpdate = {this.handleEnableNextStep} />;
      case 3:
        return <Model handlerToUpdate = {this.handleEnableNextStep} />;
      case 4:
        return <Floorplan handlerToUpdate = {this.handleEnableNextStep} />
      default:
        return ''
    }
  }

  render() {
    const { classes } = this.props;
    const steps = this.getSteps();
    const { activeStep } = this.state;   
    
    return (
      <div className={classes.root}>
        <Stepper activeStep={activeStep} >
          {steps.map((label, index) => {
            const props = {};
            const labelProps = {};
            if (this.isStepOptional(index)) {
              labelProps.optional = <Typography variant="caption">Optional</Typography>;
            }
            if (this.isStepSkipped(index)) {
              props.completed = false;
            }
            // if (this.isNewOrPreowned(index)) {
            //   form = <NewOrPreowned />
            // }
            return (
              <Step key={label} {...props}>
                <StepLabel {...labelProps}>{label}</StepLabel>                
              </Step>
            );
          })}
        </Stepper>
        <div>
          {activeStep === steps.length ? (
            <div>
              <Typography component={'span'}  className={classes.instructions}>
                All steps completed - you&apos;re finished
              </Typography>
              <Button onClick={this.handleReset} className={classes.button}>
                Reset
              </Button>
            </div>
          ) : (
            <div>
              <Typography component={'span'}  className={classes.instructions}>{this.getStepContent(activeStep)}</Typography>
              <div>
                <Button
                  disabled={activeStep === 0}
                  onClick={this.handleBack}
                  className={classes.button}
                >
                  Back
                </Button>
                {this.isStepOptional(activeStep) && (
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={this.handleSkip}
                    className={classes.button}
                  >
                    Skip
                  </Button>
                )}
                <Button
                  variant="contained"
                  color="primary"
                  onClick={activeStep === steps.length - 1 ? this.handleCard :this.handleNext}
                  className={classes.button}
                  disabled={this.state.nextStepDisabled}
                >
                  {activeStep === steps.length - 1 ? 'See RV card' : 'Next'}
                </Button>
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }
}

HorizontalLinearStepper.propTypes = {
  classes: PropTypes.object,
};

export default withRouter(withStyles(styles)(HorizontalLinearStepper));